.text-content {
  font-family: Helvetica, arial, sans-serif;
  font-size: 14px;
  line-height: 1.6;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: white;
  padding: 30px;
  color: #333;
}
.text-content > *:first-child {
  margin-top: 0 !important;
}
.text-content > *:last-child {
  margin-bottom: 0 !important;
}
.text-content a {
  color: #4183C4;
  text-decoration: none;
}
.text-content a.absent {
  color: #cc0000;
}
.text-content a.anchor {
  display: block;
  padding-left: 30px;
  margin-left: -30px;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
}
.text-content h1,
.text-content h2,
.text-content h3,
.text-content h4,
.text-content h5,
.text-content h6 {
  margin: 20px 0 10px;
  padding: 0;
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  cursor: text;
  position: relative;
}
.text-content h2:first-child,
.text-content h1:first-child,
.text-content h1:first-child + h2,
.text-content h3:first-child,
.text-content h4:first-child,
.text-content h5:first-child,
.text-content h6:first-child {
  margin-top: 0;
  padding-top: 0;
}
.text-content h1:hover a.anchor,
.text-content h2:hover a.anchor,
.text-content h3:hover a.anchor,
.text-content h4:hover a.anchor,
.text-content h5:hover a.anchor,
.text-content h6:hover a.anchor {
  text-decoration: none;
}
.text-content h1 tt,
.text-content h1 code {
  font-size: inherit;
}
.text-content h2 tt,
.text-content h2 code {
  font-size: inherit;
}
.text-content h3 tt,
.text-content h3 code {
  font-size: inherit;
}
.text-content h4 tt,
.text-content h4 code {
  font-size: inherit;
}
.text-content h5 tt,
.text-content h5 code {
  font-size: inherit;
}
.text-content h6 tt,
.text-content h6 code {
  font-size: inherit;
}
.text-content h1 {
  font-size: 28px;
  color: black;
}
.text-content h2 {
  font-size: 24px;
  border-bottom: 1px solid #cccccc;
  color: black;
}
.text-content h3 {
  font-size: 18px;
}
.text-content h4 {
  font-size: 16px;
}
.text-content h5 {
  font-size: 14px;
}
.text-content h6 {
  color: #777777;
  font-size: 14px;
}
.text-content p,
.text-content blockquote,
.text-content ul,
.text-content ol,
.text-content dl,
.text-content li,
.text-content table,
.text-content pre {
  margin: 15px 0;
}
.text-content hr {
  background: transparent url("http://tinyurl.com/bq5kskr") repeat-x 0 0;
  border: 0 none;
  color: #cccccc;
  height: 4px;
  padding: 0;
}
.text-content > h2:first-child {
  margin-top: 0;
  padding-top: 0;
}
.text-content > h1:first-child {
  margin-top: 0;
  padding-top: 0;
}
.text-content > h1:first-child + h2 {
  margin-top: 0;
  padding-top: 0;
}
.text-content > h3:first-child,
.text-content > h4:first-child,
.text-content > h5:first-child,
.text-content > h6:first-child {
  margin-top: 0;
  padding-top: 0;
}
.text-content a:first-child h1,
.text-content a:first-child h2,
.text-content a:first-child h3,
.text-content a:first-child h4,
.text-content a:first-child h5,
.text-content a:first-child h6 {
  margin-top: 0;
  padding-top: 0;
}
.text-content h1 p,
.text-content h2 p,
.text-content h3 p,
.text-content h4 p,
.text-content h5 p,
.text-content h6 p {
  margin-top: 0;
}
.text-content li p.first {
  display: inline-block;
}
.text-content ul,
.text-content ol {
  padding-left: 30px;
}
.text-content ul :first-child,
.text-content ol :first-child {
  margin-top: 0;
}
.text-content ul :last-child,
.text-content ol :last-child {
  margin-bottom: 0;
}
.text-content dl {
  padding: 0;
}
.text-content dl dt {
  font-size: 14px;
  font-weight: bold;
  font-style: italic;
  padding: 0;
  margin: 15px 0 5px;
}
.text-content dl dt:first-child {
  padding: 0;
}
.text-content dl dt > :first-child {
  margin-top: 0;
}
.text-content dl dt > :last-child {
  margin-bottom: 0;
}
.text-content dl dd {
  margin: 0 0 15px;
  padding: 0 15px;
}
.text-content dl dd > :first-child {
  margin-top: 0;
}
.text-content dl dd > :last-child {
  margin-bottom: 0;
}
.text-content blockquote {
  border-left: 4px solid #dddddd;
  padding: 0 15px;
  color: #777777;
}
.text-content blockquote > :first-child {
  margin-top: 0;
}
.text-content blockquote > :last-child {
  margin-bottom: 0;
}
.text-content table {
  padding: 0;
}
.text-content table tr {
  border-top: 1px solid #cccccc;
  background-color: white;
  margin: 0;
  padding: 0;
}
.text-content table tr:nth-child(2n) {
  background-color: #f8f8f8;
}
.text-content table tr th {
  font-weight: bold;
  border: 1px solid #cccccc;
  text-align: left;
  margin: 0;
  padding: 6px 13px;
}
.text-content table tr td {
  border: 1px solid #cccccc;
  text-align: left;
  margin: 0;
  padding: 6px 13px;
}
.text-content table tr th :first-child,
.text-content table tr td :first-child {
  margin-top: 0;
}
.text-content table tr th :last-child,
.text-content table tr td :last-child {
  margin-bottom: 0;
}
.text-content img {
  max-width: 100%;
}
.text-content span.frame {
  display: block;
  overflow: hidden;
}
.text-content span.frame > span {
  border: 1px solid #dddddd;
  display: block;
  float: left;
  overflow: hidden;
  margin: 13px 0 0;
  padding: 7px;
  width: auto;
}
.text-content span.frame span img {
  display: block;
  float: left;
}
.text-content span.frame span span {
  clear: both;
  color: #333333;
  display: block;
  padding: 5px 0 0;
}
.text-content span.align-center {
  display: block;
  overflow: hidden;
  clear: both;
}
.text-content span.align-center > span {
  display: block;
  overflow: hidden;
  margin: 13px auto 0;
  text-align: center;
}
.text-content span.align-center span img {
  margin: 0 auto;
  text-align: center;
}
.text-content span.align-right {
  display: block;
  overflow: hidden;
  clear: both;
}
.text-content span.align-right > span {
  display: block;
  overflow: hidden;
  margin: 13px 0 0;
  text-align: right;
}
.text-content span.align-right span img {
  margin: 0;
  text-align: right;
}
.text-content span.float-left {
  display: block;
  margin-right: 13px;
  overflow: hidden;
  float: left;
}
.text-content span.float-left span {
  margin: 13px 0 0;
}
.text-content span.float-right {
  display: block;
  margin-left: 13px;
  overflow: hidden;
  float: right;
}
.text-content span.float-right > span {
  display: block;
  overflow: hidden;
  margin: 13px auto 0;
  text-align: right;
}
.text-content code,
.text-content tt {
  margin: 0 2px;
  padding: 0 5px;
  white-space: nowrap;
  border: 1px solid #eaeaea;
  background-color: #f8f8f8;
  border-radius: 3px;
}
.text-content pre code {
  margin: 0;
  padding: 0;
  white-space: pre;
  border: none;
  background: transparent;
}
.text-content .highlight pre {
  background-color: #f8f8f8;
  border: 1px solid #cccccc;
  font-size: 13px;
  line-height: 19px;
  overflow: auto;
  padding: 6px 10px;
  border-radius: 3px;
}
.text-content pre {
  background-color: #f8f8f8;
  border: 1px solid #cccccc;
  font-size: 13px;
  line-height: 19px;
  overflow: auto;
  padding: 6px 10px;
  border-radius: 3px;
}
.text-content pre code,
.text-content pre tt {
  background-color: transparent;
  border: none;
}
.text-content pre code p {
  margin: 0;
}



