.mocker-menu .xhr {
  position: relative;
}
.mocker-menu .xhr::before {
  position: absolute;
  top: 5px;
  left: 0;
  width: 30px;
  height: 30px;
  content: ' ';
  background-repeat: no-repeat;
  background-size: cover;
}
.mocker-menu .xhr.post::before {
  background-image: url('../../../../../asserts/img/post.png');
}
.mocker-menu .xhr.get::before {
  background-image: url('../../../../../asserts/img/get.png');
}



