.mocker-list-item {
  position: relative;
}
.mocker-list-item .detail {
  margin-bottom: 10px;
}
.mocker-list-item .card-action .set-disable-btn {
  margin-right: 5px;
}
.mocker-list-item .xhr {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
  background-size: cover;
}
.mocker-list-item .xhr.post {
  background-image: url('../../../../../asserts/img/post.png');
}
.mocker-list-item .xhr.get {
  background-image: url('../../../../../asserts/img/get.png');
}



